import {ActionTypeTask, tasksReducer} from './tasks-reducer';
import {ActionTypeTodolist, todolistsReducer} from './todolists-reducer';
import {applyMiddleware, combineReducers, createStore} from 'redux';
import thunkMiddleware, {ThunkAction} from "redux-thunk";
import {ActionsTypeApp, appReducer} from "./app-reduser";

//  reducer-   combineReducers,
//      -
const rootReducer = combineReducers({
    tasks: tasksReducer,
    todolists: todolistsReducer,
    app: appReducer
})

//   store
export const store = createStore(rootReducer, applyMiddleware(thunkMiddleware));

//      
export type AppRootStateType = ReturnType<typeof rootReducer>
export type ActionType = ActionTypeTodolist | ActionTypeTask | ActionsTypeApp

export type AppThunk<ReturnType = void> = ThunkAction<
    ReturnType,
    AppRootStateType,
    unknown,
    ActionType
    >

//  ,         store   
// @ts-ignore
window.store = store;
